import Vue from 'vue'
import VueRouter from 'vue-router'
import MainLayout from '../layouts/Main.vue'
import AuthLayout from '../layouts/Auth.vue'

Vue.use(VueRouter)


const routes = [
  {
    path: '/',
    name: 'auth',
    component: AuthLayout,
    children: [
      {
        path: '/',
        name: 'login',
        component: () => import(/* webpackChunkName: "about" */ '../views/auth/Login.vue')
      },
      {
        path: '/register',
        name: 'register',
        component: () => import(/* webpackChunkName: "about" */ '../views/auth/Register.vue')
      }]
  },
  {
    path: '/',
    name: 'main',
    component: MainLayout,
    children: [
      {
        path: '/dashboard',
        name: 'dashboard',
        component: () => import(/* webpackChunkName: "about" */ '../views/main/Dashboard.vue')
      },
      {
        path: '/admin',
        name: 'admin',
        component: () => import(/* webpackChunkName: "about" */ '../views/main/views/AdminView.vue')
      },
      {
        path: '/grower',
        name: 'grower',
        component: () => import(/* webpackChunkName: "about" */ '../views/main/views/GrowerView.vue')
      },
      {
        path: '/mill',
        name: 'mill',
        component: () => import(/* webpackChunkName: "about" */ '../views/main/views/MillView.vue')
      },
      {
        path: '/growers',
        name: 'growers',
        component: () => import(/* webpackChunkName: "about" */ '../views/main/views/GrowersView.vue')
      }
    ]
  }]

const router = new VueRouter({
  routes
})

export default router
